html, body {
  padding: 0;
  margin: 0;
}

h1 {
  font-family: "Helvetica";
  font-size: 20px;
  font-weight: 600;
  margin: 2px;
}

p {
  font-family: "Arial";
  font-size: 16px;
  font-weight: normal;
  font-style: oblique;
}

.white {
  color: #fff;
  padding: 5px;
  border: 1px solid black;
  background: linear-gradient(120deg, #00000050, #ffd70050);
  border-radius: 8px;
}

.logo {
  width: 50px;
  height: inherit;
}

.logo img {
  height: inherit;
  width: inherit;
}

.first {
  background-color: #759DD1;
  width: 90vw;
  height: 95vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 40px;
  margin-top: 0;
  margin-bottom: 5px;
}

nav .menu img {
  width: 30px;
}

nav #navigate {
  display: none;
}

.navMenu {
  background-color: #0FEB4230;
  border: 2px solid #707070;
  display: block;
  width: 151px;
  height: 240px;
  padding: 11px;
  position: absolute;
  right: 0;
  top: -10px;
  margin: 0;
  z-index: 5;
  backdrop-filter: blur(10px);
}

.navMenu li {
  list-style-type: none;
  cursor: pointer;
  border: 1px solid #fff;
  background-color: #F1E0E020;
  margin-bottom: 20px;
  text-align: center;
  padding: 8px 0 0;
  height: 29px;
  vertical-align: middle;
}

.navMenu li.active {
  color: #F1E0E0;
  border-color: aqua;
}

.navMenu li button {
  background: none;
  border: none;
}

.hero {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-top: 0;
}

.hero img {
  width: 306px;
  height: 174px;
  opacity: .8;
  z-index: 1;
  display: block;
}

.hero img#p2 {
  margin-top: -60px;
}

.hero h1 {
  text-align: center;
  z-index: 2;
  width: 200px;
  margin-bottom: 0;
}

.hero p {
  z-index: 3;
  margin: 0;
}

section {
  height: 500px;
  width: 90%;
  margin: 20px 0 0;
  background: #D2D2D240;
}

section h1 {
  font-family: "Fira code";
  margin: 3px 10px;
}

section.about {
  position: relative;
  width: 90%;
  z-index: 1;
}

section.about .group {
  padding-left: 10px;
}

section.about .group #plane2 {
  height: 200px;
  padding-top: .75;
  margin: 0 auto 5px;
}

section.about .group .service {
  margin-top: 5px;
}

section.about .group .service .hand {
  padding: 5px;
}

section.about::after {
  content: '';
  background: url("../assets/globe.jpg");
  background-position: center;
  border-top-right-radius: 40px;
  padding: 0;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: inherit;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: .4;
  z-index: -1;
  margin: 0;
}

section.contact form {
  padding: 5px 20px;
}

section.contact form legend {
  font-weight: 600;
  font-family: cursive;
}

section.contact form .label {
  margin-bottom: 15px;
}

section.contact form input {
  height: 20px;
  box-shadow: inset 1px 1px 1px #fff;
}

section.contact form input[type=submit] {
  background: #707070;
  padding: .3rem;
  height: fit-content;
  font-family: Arial, Helvetica, sans-serif;
  border: 1.5px solid #D2D2D240;
}

section.contact form input[type=submit]:hover {
  background-color: #D2D2D2;
  color: #0C60CE;
}

section.contact form input, section.contact form textarea {
  display: block;
  background-color: #F1E0E0;
  border: 1px solid black;
  border-radius: 5px;
}

section.contact form .whatsapp {
  background-color: #D2D2D2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: 3px auto 0;
}

section.contact form .whatsapp button {
  color: green;
  font-size: 10px;
}

section.contact .media {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3px;
  width: 80%;
  margin: 3px auto 0;
  background-color: #D2D2D2;
}

section#testimonials {
  height: auto;
}

section#testimonials h1 {
  margin: 0 0 initial;
}

section#testimonials .image-home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section#testimonials .images {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5px 5px;
  position: relative;
}

section#testimonials .images img {
  width: 110px;
  height: 110px;
}

section#testimonials .images img:hover {
  position: absolute;
  width: 1100px;
  height: 110px;
}

section.footer {
  height: auto;
  background: linear-gradient(120deg, #0C60CE, gold);
  position: relative;
  width: 95%;
  margin-bottom: 0;
}

section.footer main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

section.footer .desc {
  padding: 10px;
}

section.footer .desc .logo {
  position: absolute;
  right: 2px;
  top: 2px;
}

section.footer .desc p {
  width: 90%;
  margin: 10px auto 0;
}

section.footer .site-map {
  padding: 10px;
  padding-bottom: 0;
}

section.footer .site-map .navMenu {
  position: static;
  margin: 0 auto;
  background: none;
  border: 0;
}

section.footer .site-map .navMenu li {
  border: 0;
}

section.footer .site-map .navMenu li:hover {
  background-color: gold;
}

section.footer .contact-us {
  margin: 0 auto;
  width: 40%;
}

section.footer .copyright {
  background-color: black;
  color: #fff;
  font-size: 12px;
}

section.footer .copyright span {
  display: block;
  width: 80%;
  margin: 0 auto;
}

.socials img {
  width: 20px;
  height: 20px;
  padding: 1px 3px;
  transition: all 1s ease-in;
}

@media only screen and (min-width: 800px) {
  nav .menu {
    display: none;
  }
  nav #navigate {
    display: block;
  }
  nav .navMenu {
    background: none;
    border: 0;
    display: block;
    width: auto;
    height: auto;
    position: absolute;
    right: 30px;
    top: -10px;
    z-index: 5;
  }
  nav .navMenu li {
    border: 0;
    background: none;
    margin-bottom: 20px;
    text-align: center;
    padding: 8px 10px 0;
    display: inline;
    height: fit-content;
  }
  nav .navMenu li.active {
    color: #F1E0E0;
    border-bottom: 1px solid aqua;
  }
  .hero p {
    width: 40%;
  }
  .about {
    height: 60vh;
  }
  .about .group {
    height: 100%;
  }
  .about .group .about-img {
    width: 30%;
    position: static;
    height: 80%;
    float: left;
    margin-right: 5%;
  }
  .about .group .about-img #plane2 {
    height: 100%;
    width: 100%;
  }
  .about .group .description, .about .group .services {
    width: 90%;
  }
  .contact #myform {
    padding: 0 20%;
  }
  .contact #myform .left {
    float: left;
    padding: 5% 20% 0 0;
  }
  .contact #myform .left label, .contact #myform .left input {
    height: 1.5rem;
  }
  .contact #myform .left label {
    margin-top: 1.5rem;
  }
  .contact #myform .left input {
    margin-bottom: .8rem;
  }
  .contact #myform input[type=submit] {
    display: block;
    margin: 5px auto;
  }
  .contact #myform .whatsapp {
    width: 40%;
  }
  .contact #media {
    width: 40%;
  }
  section#testimonials #images {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  section#testimonials #images img {
    width: 150px;
    height: 150px;
  }
  section.footer {
    width: 90%;
  }
  section.footer main {
    flex-direction: row;
    align-items: flex-start;
    padding: 1rem;
  }
  section.footer main .desc {
    width: 60%;
  }
  section.footer .contact-us {
    position: absolute;
    top: 60%;
    left: 10%;
  }
  section.footer .logo {
    top: 5%;
    left: 20%;
  }
}
